<template>
  <div class="goods-content">
    <page-table
      :theadValues="theadValues"
      :tbodyList="tbodyList"
      tableHeaderTitle=""
    />
  </div>
</template>

<script setup>
import { computed, ref, h } from 'vue'
import { useStore } from 'vuex'
import { NImage, NButton } from 'naive-ui'
import PageTable from '@/components/page-table/page-table.vue'

const store = useStore()

store.dispatch('productModule/getPageListAction', {
  pageUrl: '/goods/list',
  queryInfo: {
    offset: 0,
    size: 100
  },
  pageName: 'product'
})

const tbodyList = computed(() => store.state.productModule.productList)

const theadValues = ref([
  {
    type: 'selection'
  },
  {
    title: '',
    key: 'sort',
    width: 60
  },
  {
    title: '',
    key: 'desc',
    ellipsis: {
      tooltip: true
    }
  },
  {
    title: '',
    key: 'oldPrice',
    width: 100
  },
  {
    title: '',
    key: 'newPrice',
    width: 100
  },
  {
    title: '',
    key: 'productImage',
    width: 100,
    render(row) {
      const productImageActions = row.productImage.map((item) => {
        return h(NImage, {
          style: {
            // width: '60px',
            height: '60px'
          },
          src: item.src
        })
      })
      return productImageActions
    }
  },
  {
    title: '',
    key: 'createAt'
  },
  {
    title: '',
    key: 'updateAt'
  },
  {
    title: '',
    key: 'actions',
    render(row) {
      const actions = row.actions.map((item) => {
        return h(
          NButton,
          {
            style: {
              marginRight: '6px'
            },
            type: item.type
          },
          {
            default: () => item.text
          }
        )
      })
      return actions
    }
  }
])
</script>

<style lang="less" scoped>
.goods-content {
  margin: 20px;
  background-color: #000;
}
</style>
